.Tabs {
    &-Menu {
        display: flex;
        border-bottom: 1px solid #E5E5E5;
        box-sizing: border-box;
        &-Item {
            text-transform: uppercase;
            font-size: 16px;
            line-height: 20px;
            color: #7f8285;
            font-weight: 600;
            padding: 6px 0;
            margin-right: 32px;
            cursor: pointer;
            &:last-child {
                margin-right: 0;
            }

            &_active {
                color: #000;
                border-bottom: 3px solid #FF3029;
                box-sizing: border-box;
            }
        }
    }
}